#landing-page { 
  background-image: url("./images/circle.png");
  background-position: center center ;
  height: 100vh;
  width: 100vw;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack:  center;
      justify-content:  center;
}

#landing-spinner {
  -ms-flex-item-align: center;
      align-self: center;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #0FFFFF;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}


#header {
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

#content-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

#profile {
  height:60vh;  
  -ms-flex: 1 1;  
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  width: 50%;
}

#stream-queue {
  max-height: 2%;
  overflow: scroll;
  /* display: flex; */
  /* flex: 1; */
  /* flex-direction: column; */
  width: 50%;  
}

.scrollable {
  height:60vh;
  overflow:auto;
}

.media-player {
  /* display: flex; */
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.profile-pic {
  height: 10%;
  width: auto;
}

.buy-button {
  margin:auto;
  height: 2vh;
  width: 1vh;
  background-color: "purple";
  color:"puple";
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



